.chat-icon {
  height: 56px;
  width: 56px;
  border-radius: 100%;
  bottom: 32px;
  right: 32px;
  position: fixed;
  cursor: pointer;
  background-color: #214796;
  box-shadow: 0px 12px 24px 0px rgb(0 0 0 / 15%);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 999;
}

.collapse {
  display: none;
}

.chat-container {
  --light-color: #fff;
  display: table;
  height: 500px;
  width: 400px;
  position: fixed;
  box-shadow: 0px 12px 24px 0px rgb(0 0 0 / 15%);
  border-radius: 10px;
  z-index: 999;
}

.chat-header {
  display: flex;
  align-items: center;
  padding: 0px 30px;
  background-color: #214796;
  color: var(--light-color);
  font-size: 1.5rem;
  cursor: pointer;
}

.chat-header .logo {
  height: 35px;
  width: 35px;
}

.chat-header img {
  height: 100%;
  width: 100%;
}

.chat-header .title {
  padding-left: 10px;
  margin-top: -8px;
}

.chat-body {
  display: flex;
  flex-direction: column;
  padding: 8px 10px;
  align-items: flex-end;
  overflow-y: scroll;
  background-color: #F5F9FC;
  overflow-x: hidden;
}

.chat-input {
  display: flex;
  align-items: center;
  border-top: 1px solid #EEE;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.input-sec {
  flex: 9;
  margin-left: 5px;
}

.send {
  flex: 1;
  padding-top: 1rem;
  padding-right: 5px;
  height: 100%;
}

.chat-input {
  line-height: 30px;
  padding: 0px 10px;
  border: none;
  outline: none;
  caret-color: black;
  font-size: 1rem;
  width: 100%;
}

.chatbot-message,
.user-message {
  padding: 12px;
  box-shadow: 0px 1px 2px 0px rgb(0 0 0 / 15%);
  max-width: 99%;
  position: relative;
  overflow: hidden;
  text-align: justify;
}

.user-message {
  width: max-content;
  background: var(--light-color);
  color: #4a4a4a;
  margin: 0px 5px 0px 0px;
  border-radius: 18px 18px 0px 18px;
  transform: translate(50%);
  animation: fade-in-right 0.3s ease-in-out both;
}

.chatbot-message {
  width: auto;
  background: #0652c0;
  color: var(--light-color);
  margin: 0px 0px 0px 5px;
  transform: translate(-50%);
  animation: fade-in-left 0.3s ease-in-out both;
}

/* Chat-bot url */
.chatbot-message-url {
  padding: 6px 10px 10px 10px;
  width: max-content;
  box-shadow: 0px 1px 2px 0px rgb(0 0 0 / 15%);
  max-width: 99%;
  position: relative;
  overflow: hidden;
  text-align: justify;
  background: var(--light-color);
  /* color: var(--light-color); */
  margin: 0px 0px 0px 5px;
  border-radius: 0px 0px 18px 18px;
  transform: translate(-50%);
  animation: fade-in-left 0.3s ease-in-out both;
}

/* Chat-bot Youtube Video */
.video-responsive {
  padding: 5px 10px 5px 10px;
  width: fit-content;
  box-shadow: 0px 1px 2px 0px rgb(0 0 0 / 15%);
  max-width: 99%;
  position: relative;
  overflow: hidden;
  background: var(--light-color);
  margin: 0px 0px 0px 5px;
  border-radius: 0px 0px 18px 18px;
  transform: translate(-50%);
  animation: fade-in-left 0.3s ease-in-out both;
}

.video-responsive iframe {
  left: 0;
  top: 0;
  height: 100%;
  width: 86%;
}

/* Chat-bot acction message */
.chatbot-message-action {
  padding: 6px 10px 10px 10px;
  width: max-content;
  box-shadow: 0px 1px 2px 0px rgb(0 0 0 / 15%);
  max-width: 99%;
  position: relative;
  overflow: hidden;
  text-align: justify;
  background: var(--light-color);
  margin: 0px 0px 5px 5px;
  border-radius: 0px 0px 18px 18px;
  transform: translate(-50%);
  animation: fade-in-left 0.3s ease-in-out both;
}

.action-message {
  border: 0.5px solid #4CABF2;
  margin-bottom: 4px;
  border-radius: 18px;
  padding: 0px 8px 1px 8px;
  max-width: 230px;
  color: #4CABF2;
  text-transform: capitalize;
  cursor: pointer;
}

.bot-response-container {
  display: flex;
  width: 99%;
  justify-content: flex-start;
  word-wrap: break-word;
  margin-bottom: 10px;
}

.bot-response-container-icon {
  padding: 5px;
  height: 35px;
  min-width: 35px;
  background: white;
  border-radius: 50% 50% 0px 50%;
  box-shadow: 0px 1px 2px 0px rgb(0 0 0 / 15%);
}

.user-response-container {
  display: flex;
  height: 45px;
  width: 99%;
  align-items: center;
  justify-content: flex-end;
  word-wrap: break-word;
  margin-bottom: 10px;
}

.user-response-container img {
  padding: 5px;
  height: 35px;
  min-width: 35px;
  background: white;
  border-radius: 50% 50% 50% 0px;
  box-shadow: 0px 1px 2px 0px rgb(0 0 0 / 15%);
}

@keyframes fade-in-right {
  0% {
    transform: translate(50%);
    opacity: 0;
  }
  100% {
    transform: translate(0%);
    opacity: 1;
  }  
}

@keyframes fade-in-left {
  0% {
    transform: translate(-50%);
    opacity: 0;
  }
  100% {
    transform: translate(0%);
    opacity: 1;
  }  
}

/* Extra small devices (phones, 600px and down) */
@media only screen and (max-width: 600px) {
  .chat-container {
    width: 100%;
    height: 100%;
    bottom: 0;
  }
  .chat-header {
    height: 8%;
  }
  .chat-body {
    height: calc(95% - 10.5%);
  }
  .chat-input {
    height: 4.5%;
  }
}

/* Small devices (portrait tablets and large phones, 600px and up) */
@media only screen and (min-width: 600px) {
  
}

/* Medium devices (landscape tablets, 768px and up) */
@media only screen and (min-width: 768px) {
  .chat-container {
    right: 32px;
    bottom: 32px;
  }
  .chat-header {
    height: 56px;
    border-top-left-radius: 15px;
    border-top-right-radius: 15px;
  }
  .chat-body {
    height: calc(500px - 105px);
  }
  .chat-input {
    height: 44px;
  }
} 

/* Large devices (laptops/desktops, 992px and up) */
@media only screen and (min-width: 992px) {
  .chat-container {
    right: 32px;
    bottom: 32px;
  }
} 

